#my-canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.game-container {
  width: 530px;
  height: 470px;
  border-radius: 10px;
  margin: 100px auto;
  box-shadow: 0 0 5px 5px gray;
}

.game-container .fa-rotate, .game-container div:not(:first-child) {
  margin: 25px;
}

.game-container .header {
  text-align: center;
  color: #fff;
  text-shadow: 0 1px 2px #23627a;
  background: #0ff;
  border-radius: 10px 10px 0 0;
  padding: 20px;
  font-size: 32px;
  font-weight: 800;
}

.game-container .player-container i, .game-container .fa-rotate {
  cursor: pointer;
}

i {
  margin-right: 20px;
  transform: scale(1.5);
}

.blue {
  color: #0ff;
}

.none {
  display: none;
}

@media (max-width: 550px) {
  .game-container {
    width: 330px;
    margin: 50px auto;
  }
}

